.FilterContainer {    
    padding: 20px;
    max-width: 480px;
}

.FilterInput {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    transition: background-color 0.5s ease-in-out;
}

.FilterInput:focus {
    background-color: #e4ffe6;
}